@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.theme-switch{align-items:center;background:transparent;border:0;color:var(--text_color);cursor:pointer;display:flex;font-size:16px;font-weight:var(--font-weight-light);grid-gap:10px;gap:10px;outline:0;text-align:center}

.header{align-items:center;background:var(--header-background);box-shadow:var(--header-box-shadow);color:var(--text_color);display:flex;height:var(--header-height);font-weight:var(--font-weight-bold);justify-content:space-between;padding-left:var(--padding-x);padding-right:var(--padding-x);position:-webkit-sticky;position:sticky;top:0;z-index:1}.header h1{font-size:24px}

.container{min-height:var(--container-min-height);padding:50px var(--padding-x)}

.input-search{align-items:center;background-color:var(--elements_color);border-radius:5px;box-shadow:var(--input-box-shadow);display:flex;height:55px;padding:0 35px}.input-search__icon{color:var(--input_color)}.input-search__element{background-color:var(--elements_color);border:0;color:var(--input_color);flex:1 1;font-size:14px;font-weight:var(--font-weight-regular);height:100%;padding:0 20px;outline:0}

.country-card{background:var(--elements_color);border-radius:5px;box-shadow:var(--card-box-shadow);display:flex;flex-direction:column;justify-content:space-between;max-width:500px;overflow:hidden}.country-card__image{align-items:baseline;display:flex;min-height:180px}.country-card__image img{width:100%}.country-card__content{color:var(--text_color);height:150px;padding:15px 20px}.country-card__title{font-size:18px;font-weight:var(--font-weight-bold);margin-bottom:10px}.country-card__label{font-size:14px;font-weight:var(--font-weight-regular)}.country-card__value{font-size:14px;font-weight:var(--font-weight-light)}

.home{display:grid;grid-gap:80px;gap:80px;grid-template-columns:repeat(auto-fit, minmax(260px, auto));margin-top:45px}.home__input{width:100%;max-width:480px}.home--loading,.home--not-found{color:var(--text_color);font-size:24px;left:50%;position:absolute;text-align:center;transform:translate3d(-50%, -50%, 0);top:50%}

body{font-family:"Nunito Sans", sans-serif;font-size:16px}input{font-family:"Nunito Sans", sans-serif}h1,h2,h3,h4,input{font-size:1rem}.light-mode{--background_color: #fafafa;--input_color: #858585;--text_color: #111517;--elements_color: #fff;--header-background: #fff;background:var(--background_color)}.dark-mode{--background_color: #202c37;--input_color: #fff;--text_color: #fff;--elements_color: #2b3945;--header-background: #2b3945;background:var(--background_color)}:root{--font-weight-light: 300;--font-weight-regular: 600;--font-weight-bold: 800;--padding-x: 80px;--container-min-height: calc(100vh - var(--header-height));--header-height: 80px;--header-box-shadow: 1px 1px 5px rgba(17,21,23,0.1);--card-box-shadow: 1px 3px 8px rgba(17,21,23,0.1);--input-box-shadow: 1px 3px 8px rgba(17,21,23,0.1)}*{padding:0;margin:0;box-sizing:border-box}ul{list-style:none}

